

*{
    margin: 30;
}
*::selection{
    background-color: rgba(225, 255, 0, 0.598);
}

.container {
    background-color: rgba(91, 97, 95, 0.61);
    margin: 100px;
    border: 2px solid black;
    border-radius: 30px;
    display: grid;
    grid-template-columns: 1.5fr 1fr
}

.container>a {
    color: black;
    text-decoration:none;
}

.container>a:visited {
    color:brown;
    text-decoration:line-through;
}

.container>ul{
    grid-column: 1;
}

.container>ul>li {
    margin: 10px; 
    font-family: Malgun Gothic;
    font-size: 9pt;
    white-space: nowrap;
}

.container>img{
    width: auto;
    border-radius: 30px;
    grid-row: 1/ span 2;
    grid-column: 2;
    border-left: 3px solid black;
}
